<template>
	<div class="userdetail">
		<!-- 导航条 -->
		<div class="nav">
			<van-nav-bar fixed @click-left="back" left-arrow>
				<template #right >
					<van-icon @click="showShare = true" name="ellipsis" size="20" color="#000" />
				</template>
			</van-nav-bar>
		</div>
		<div class="user-info">
			<div class="img-box">
				<div class="user-img">
					<img class="auto-img" :src="user.user_photo" alt="" />
				</div>
				<div class="verified" v-if="user.verified_image">
					<img class="auto-img" :src="user.verified_image" alt="" />
				</div>
			</div>
			<div class="info">
				<div class="title-box">
					<div class="name">{{ user.nick }}</div>
					<div class="usermedal-box" v-if="user.usermedal">
						<div class="usermedal">
							<img class="auto-img" :src="user.usermedal.icon" alt="" />
						</div>
						<div class="count">{{ user.usermedal.count_text }}</div>
					</div>
					<div class="lv">LV.{{ user.lvl }}</div>
				</div>
				<div class="collect interest" @click.stop="interest">关注</div>
			</div>
		</div>
		<div class="archivement">
			<div
				class="tag"
				v-for="(tag, index) in archivementTag"
				:key="index"
				:style="{
					color: tag.text_color,
					backgroundColor: tag.background_start_color,
				}"
			>
				{{ tag.text }} <van-icon name="play" />
			</div>
		</div>
		<div class="introduce">
			<div class="item">厨娘</div>
			<div class="item">{{ user.age }}</div>
			<div class="item">{{ user.location }}</div>
		</div>
		<div class="introduction">
			{{ user.introduction ? user.introduction : text }}
		</div>
		<div class="list">
			<div class="item">
				<div class="count">{{ user.following_count_text }}</div>
				<div class="title">关注</div>
			</div>
			<div class="item">
				<div class="count">{{ user.followers_count_text }}</div>
				<div class="title">粉丝</div>
			</div>
			<div class="item">
				<div class="count">{{ user.favoed_count_text }}</div>
				<div class="title">被收藏</div>
			</div>
			<div class="item">
				<div class="count">{{ user.noted_count_text }}</div>
				<div class="title">被学做</div>
			</div>
			<div class="item">
				<div class="count">{{ user.point_text }}</div>
				<div class="title">经验值</div>
			</div>
		</div>
		<van-tabs
			v-model="activeName"
			color="#facc30"
			title-active-color="#000"
			title-inactive-color="#999"
		>
			<!-- 菜谱 -->
			<van-tab
				:title="name.recipes"
				:title-style="{ fontWeight: 'bold', fontSize: '16px' }"
				name="a"
			>
				<div class="recipes-list">
					<div
						class="recipes-item"
						v-for="(item, index) in recipes"
						:key="index"
						@click="goDetail(item.r.id)"
					>
						<div class="banner">
							<img class="auto-img" :src="item.r.img" alt="" />
						</div>
						<div class="recommend">{{ item.r.recommend_label }}</div>
						<div class="title">{{ item.r.n }}</div>
					</div>

					<van-list
						offset="100"
						v-model="loading"
						:finished="finished"
						finished-text="没有更多了"
						@load="onLoad"
					>
						<br />
					</van-list>
				</div>
			</van-tab>
			<!-- 笔记 -->
			<van-tab
				:title="name.notes"
				:title-style="{ fontWeight: 'bold', fontSize: '16px' }"
				name="b"
			>
				<van-calendar
					:min-date="minDate"
					:max-date="maxDate"
					:poppable="false"
					:show-subtitle="false"
					:show-confirm="false"
					:style="{ height: '450px' }"
				/>
				<div class="usernotescalendar-list">
					<div
						class="usernotescalendar-item"
						v-for="(item, index) in usernotescalendar"
						:key="index"
					>
						<div class="date">
							<div class="day">{{ item.day_text }}</div>
							<div class="month">{{ item.month_text }}</div>
						</div>
						<div class="notes-item">
							<div
								class="item"
								@click="goNotesDetail(note.id)"
								v-for="(note, noteIndex) in item.day_list"
								:key="noteIndex"
							>
								<img :src="note.image_u" alt="" />
							</div>
						</div>
					</div>
				</div>

				<van-list
					offset="100"
					v-model="loading"
					:finished="finished"
					finished-text="没有更多了"
					@load="onLoad2"
				>
					<br />
				</van-list>
			</van-tab>
			<!-- 收藏 -->
			<van-tab
				:title="name.collect"
				:title-style="{ fontWeight: 'bold', fontSize: '16px' }"
				name="c"
			>
				<div class="ufavorites-list">
					<div
						@click="goDetail(item.id)"
						class="ufavorites-item"
						v-for="(item, index) in ufavorites"
						:key="index"
					>
						<div class="banner">
							<img class="auto-img" :src="item.img" alt="" />
						</div>
						<div class="info">
							<div class="title">{{ item.n }}</div>
							<div class="recommend">
								<span v-show="item.rate > 0">{{ item.rate }}分 · </span
								><span>{{ item.recommend_label }}</span>
							</div>
							<div class="user">
								<div class="head-img">
									<img :src="item.a.p" alt="" class="auto-img" />
								</div>
								<div class="name">{{ item.a.n }}</div>
							</div>
						</div>
					</div>
				</div>

				<van-list
					offset="100"
					v-model="loading"
					:finished="finished"
					finished-text="没有更多了"
					@load="onLoad1"
				>
					<br />
				</van-list>
			</van-tab>
		</van-tabs>
		<van-share-sheet
			v-model="showShare"
			title="立即分享给好友"
			:options="options"
			@select="onSelect"
		/>
	</div>
</template>

<script>
import { Toast } from 'vant';
import { Dialog } from "vant";
export default {
	name: "Userdetail",
	data() {
		return {
			id: "",
			user: {},
			archivementTag: [],
			text: "吃货不是在吃就是在去吃的路上，没时间写签名",
			activeName: "a",
			offset: 0,
			limit: 10,
			noteOffset: 0,
			noteLimit: 100,
			name: {
				recipes: "",
				notes: "",
				collect: "",
			},
			year: "",
			month: "",
			day: "",
			recipes: [],
			loading: false,
			finished: false,
			ufavorites: [],
			minDate: "",
			maxDate: "",
			usernotescalendar: [],
			showShare: false,
			options: [
				[
					{ name: "微信", icon: "wechat" },
					{ name: "朋友圈", icon: "wechat-moments" },
					{ name: "微博", icon: "weibo" },
					{ name: "QQ", icon: "qq" },
				],
				[
					{ name: "复制链接", icon: "link" },
					{ name: "分享海报", icon: "poster" },
					{ name: "二维码", icon: "qrcode" },
					{ name: "小程序码", icon: "weapp-qrcode" },
				],
			],
		};
	},
	created() {
		this.id = this.$route.params.id;
		
		this.getUserDetailData();
		this.getUrecipesData();
		let D = new Date();
		this.year = D.getFullYear();
		this.month = D.getMonth();
		this.day = D.getDate();
		this.maxDate = new Date(this.year, this.month, this.day);
		this.getNotescalendar();
		this.getUfavorites();
		this.getUsernotescalendar();
	},
	methods: {
		back() {
			this.$router.go(-1);
		},

		onLoad() {
			this.offset = this.offset + 10;
			setTimeout(() => {
				this.axios({
					method: "get",
					url:
						"https://apis.netstart.cn/douguo/user/urecipes/" +
						this.id +
						"/" +
						this.offset +
						"/" +
						this.limit,
				})
					.then((result) => {
						if (result.status === 200) {
							result.data.result.list.forEach((e) => {
								this.recipes.push(e);
							});
						}
					})
					.catch((err) => {});
				this.loading = false;
				if (this.recipes.length < this.limit) {
					this.finished = true;
				}
			}, 1000);
		},

		getUserDetailData() {
			this.axios({
				method: "get",
				url: "https://apis.netstart.cn/douguo/user/info/" + this.id,
			})
				.then((result) => {
					this.archivementTag = result.data.result.user.archivement_tags;
					this.user = result.data.result.user;
					this.name.recipes = "菜谱" + this.user.recipes_count;
					this.name.notes = "笔记" + this.user.notes_count;
					this.name.collect = "收藏" + this.user.collect_count;
					let year = this.user.regist_time.split(/ /)[0].split(/-/)[0];
					let month = this.user.regist_time.split(/ /)[0].split(/-/)[1] - 1;
					let day = this.user.regist_time.split(/ /)[0].split(/-/)[2];
					this.minDate = new Date(year, month, day);
				})
				.catch((err) => {
					
				});
		},

		getUrecipesData() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/user/urecipes/" +
					this.id +
					"/" +
					this.offset +
					"/" +
					this.limit,
			})
				.then((result) => {
					this.recipes = result.data.result.list;
				})
				.catch((err) => {
					
				});
		},

		goDetail(id) {
			this.$router.push({ name: "Detail", params: { id } });
		},

		goNotesDetail(id) {
			this.$router.push({ name: "Notesdetail", params: { id } });
		},
		// 日历
		getNotescalendar() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/user/notescalendar/" +
					this.id +
					"/" +
					this.year +
					"/" +
					this.month,
			})
				.then((result) => {})
				.catch((err) => {
					
				});
		},

		// 用户发布笔记
		getUsernotescalendar() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/user/usernotescalendar/" +
					this.id +
					"/" +
					this.noteOffset +
					"/" +
					this.noteLimit,
			})
				.then((result) => {
					
					this.usernotescalendar = result.data.result.notes;
				})
				.catch((err) => {
					
				});
		},

		onLoad2() {
			this.noteOffset = this.noteOffset + 50;
			setTimeout(() => {
				this.axios({
					method: "get",
					url:
						"https://apis.netstart.cn/douguo/user/usernotescalendar/" +
						this.id +
						"/" +
						this.noteOffset +
						"/" +
						this.noteLimit,
				})
					.then((result) => {
						 (
							"result.data.result.notes ==> ",
							result.data.result.notes
						);
						if (result.status === 200) {
														
						}
						this.loading = false;
					})
					.catch((err) => {});

				if (this.usernotescalendar.length < this.noteLimit) {
					this.finished = true;
				}
			}, 1000);
		},
		// 用户收藏
		getUfavorites() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/user/ufavorites/" +
					this.id +
					"/" +
					this.offset +
					"/" +
					this.limit,
			})
				.then((result) => {
					
					this.ufavorites = result.data.result.recipes;
				})
				.catch((err) => {
					
				});
		},

		onLoad1() {
			this.offset = this.offset + 10;
			setTimeout(() => {
				this.axios({
					method: "get",
					url:
						"https://apis.netstart.cn/douguo/user/ufavorites/" +
						this.id +
						"/" +
						this.offset +
						"/" +
						this.limit,
				})
					.then((result) => {
						if (result.status === 200) {
							result.data.result.recipes.forEach((e) => {
								this.ufavorites.push(e);
							});
						}
					})
					.catch((err) => {});
				this.loading = false;
				if (this.ufavorites.length < this.limit) {
					this.finished = true;
				}
			}, 1000);
		},

		onSelect(option) {
      Toast(option.name);
      this.showShare = false;
    },

		interest() {
			let interest = document.querySelector(".interest");
			if (!this.$root.isLogin) {
				
				this.$router.push({ name: "Login" });
			} else {
				if (!this.isinterest) {
					interest.innerText = "已关注";
					interest.style.backgroundColor = "#f1f1f1";
					this.isinterest = true;
				} else {
					Dialog.confirm({
						title: "提示",
						message: "确定要取消关注该用户吗?",
					}).then(() => {
						
						interest.innerText = "关注";
						interest.style.backgroundColor = "#facc30";

						
					});
					this.isinterest = false;
				}
			}
		},
	},
};
</script>

<style lang="less" scoped>
.userdetail {
	padding-top: 50px;
	.user-info {
		display: flex;
		padding: 10px;
		.img-box {
			position: relative;
			.user-img {
				width: 80px;
				height: 80px;
				border-radius: 50%;
				overflow: hidden;
			}
			.verified {
				width: 25px;
				height: 25px;
				position: absolute;
				bottom: 0;
				right: 0;
			}
		}

		.info {
			align-items: center;
			width: calc(100% - 100px);
			margin-left: 10px;
			.title-box {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				margin-bottom: 10px;
				.name {
					font-size: 20px;
				}
				.usermedal-box {
					margin-left: 8px;
					position: relative;
					.usermedal {
						width: 65px;
					}
					.count {
						position: absolute;
						font-size: 12px;
						top: 6px;
						right: 8px;
					}
				}
				.lv {
					margin-left: 5px;
					font-style: italic;
					font-weight: bold;
					color: rgb(224, 199, 9);
				}
			}
			.collect {
				width: 100%;
				height: 40px;
				line-height: 40px;
				border-radius: 20px;
				text-align: center;
				font-size: 18px;
				background-color: #facc30;
			}
		}
	}
	.archivement {
		display: flex;
		padding: 10px;
		flex-wrap: wrap;
		.tag {
			padding: 0 10px;
			font-size: 14px;
			height: 30px;
			line-height: 30px;
			border-radius: 15px;
			background: #e5e6ef;
			margin: 5px;
		}
	}
	.introduce {
		display: flex;
		font-size: 14px;
		.item {
			margin-left: 10px;
		}
	}
	.introduction {
		padding: 10px;
		font-size: 14px;
		line-height: 24px;
	}
	.list {
		padding: 10px;
		display: flex;
		align-items: center;
		.item {
			text-align: center;
			flex: 1;
			.count {
				font-size: 16px;
				margin-bottom: 5px;
			}
			.title {
				font-size: 12px;
				color: #666;
			}
		}
	}
	.recipes-list {
		.recipes-item {
			margin-top: 5px;
			margin-bottom: 50px;
			.banner {
				display: flex;
				align-items: center;
				width: 100%;
				height: 240px;
				overflow: hidden;
			}
			.recommend {
				padding: 10px;
				font-size: 12px;
				font-weight: bold;
			}
			.title {
				padding: 0 10px;
				font-size: 14px;
			}
		}
	}
	.usernotescalendar-list {
		padding: 10px;
		.usernotescalendar-item {
			display: flex;
			.date {
				width: 90px;
				display: flex;
				align-items: start;
				.day {
					font-size: 30px;
					line-height: 30px;
					font-weight: bold;
				}
				.month {
					margin-top: 8px;
					line-height: 20px;
					color: #999;
					margin-left: 5px;
				}
			}
			.notes-item {
				flex: 1;
				display: flex;
				flex-wrap: wrap;
				.item {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 86px;
					height: 86px;
					overflow: hidden;
					margin: 1px;
				}
			}
		}
	}
	.ufavorites-list {
		padding: 20px 10px 0;
		.ufavorites-item {
			display: flex;
			height: 120px;
			overflow: hidden;
			align-items: center;
			margin-bottom: 15px;
			.title {
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
				overflow: hidden;
			}
			.banner {
				display: flex;
				align-items: center;
				flex: 1;
				border-radius: 5px;
				overflow: hidden;
			}
			.info {
				padding: 0 10px;
				flex: 1;
				.recommend {
					font-size: 12px;
					margin-top: 5px;
					margin-bottom: 10px;
				}
			}
			.user {
				display: flex;
				.head-img {
					width: 20px;
					height: 20px;
					border-radius: 50%;
					overflow: hidden;
				}
				.name {
					margin-left: 5px;
					font-size: 14px;
				}
			}
		}
	}	
}


/deep/.van-calendar__selected-day{
	background-color: #facc30;
	border-radius: 50%;
}
</style>
